import {
  Avatar,
  BorderBox,
  Box,
  Heading,
  Link as PrimerLink,
  StyledOcticon,
  Text
} from "@primer/components";
import {
  CircuitBoard,
  Link,
  Location,
  Mail,
  MarkGithub,
  Organization
} from "@primer/octicons-react";
import { graphql, useStaticQuery } from "gatsby";
import React from "react";
import Emoji from "react-emoji-render";
import Toggle from "react-toggle";
import styled from "styled-components";
import { Dev, Linkedin, Twitter } from "styled-icons/fa-brands";

import useSiteMetadata from "../hooks/siteMetaData";
import useThemeContext from "../hooks/themeContext";

const StyledHr = styled.hr`
  ${props => props.color === "dark" && "border-top-color: whitesmoke;"}
`;

export default ({ metaData }) => {
  const { layout, social } = useSiteMetadata();
  const { style, theme, setTheme } = useThemeContext();

  function onThemeChange(e) {
    const newStyle = e.target.checked ? "dark" : "light";
    setTheme(newStyle);
  }

  const {
    github: { viewer: user }
  } = useStaticQuery(
    graphql`
      query {
        github {
          viewer {
            name
            email
            bio
            login
            url
            avatarUrl
            isHireable
            location
            company
            followers {
              totalCount
            }
            isDeveloperProgramMember
            websiteUrl
            organizations(first: 10) {
              nodes {
                avatarUrl
              }
            }
          }
        }
      }
    `
  );
  const StackedMeta = props => (
    <Box {...props} display={[null, null, "inline-block"]} mr={3} />
  );

  const SideMeta = props => (
    <Box {...props} display={"flex"} alignItems={"center"} mb={3} />
  );

  const MetaComponent = layout === "stacked" ? StackedMeta : SideMeta;

  const {
    websiteUrl,
    bio,
    login,
    email,
    organizations,
    avatarUrl,
    isHireable,
    name,
    location,
    isDeveloperProgramMember,
    company
  } = user;
  return (
    <>
      <Avatar
        src={avatarUrl}
        alt="user-avatar"
        className="circle"
        mb={3}
        size={150}
        borderRadius={"50%"}
        maxWidth={"150px"}
      />
      <Heading style={{ color: theme.iconColor }} mb={2} lh={"condensed"}>
        {name ? name : login}
      </Heading>
      <Box fontSize={4} mb={2}>
        {name && (
          <MetaComponent>
            <StyledOcticon
              color={theme.iconColor}
              icon={MarkGithub}
              size={20}
              verticalAlign="middle"
              mr={2}
              ariaLabel="GitHub"
            />
            <PrimerLink
              href={`https://github.com/${login}`}
              target="_blank"
              color={theme.iconColor}
            >
              {login}
            </PrimerLink>
          </MetaComponent>
        )}
        {social.dev && (
          <PrimerLink href={social.dev} color={theme.iconColor} target="_blank">
            <Dev
              size={20}
              color={theme.iconColor}
              title={`${name}'s DEV Profile`}
            />
          </PrimerLink>
        )}
        {social.twitter && (
          <PrimerLink
            href={social.twitter}
            color={theme.iconColor}
            marginLeft={2}
            target="_blank"
          >
            <Twitter
              size={20}
              color={theme.iconColor}
              title={`${name}'s Twitter Profile`}
            />
          </PrimerLink>
        )}
        {social.linkedIn && (
          <PrimerLink
            href={social.linkedIn}
            color={theme.iconColor}
            marginLeft={2}
            target="_blank"
          >
            <Linkedin
              size={20}
              color={theme.iconColor}
              title={`${name}'s LinkedIn Profile`}
            />
          </PrimerLink>
        )}
      </Box>
      <StyledHr color={style} />
      {isDeveloperProgramMember && (
        <MetaComponent color={theme.iconColor}>
          <StyledOcticon
            color={theme.iconColor}
            icon={CircuitBoard}
            size={20}
            verticalAlign="middle"
            mr={2}
            ariaLabel="Location"
          />
          <BorderBox
            as={"span"}
            fontSize={5}
            border={0}
            color={"white"}
            bg={"green.5"}
            py={1}
            px={2}
            title="Developer Program Member"
            display={"inline-block"}
            borderRadius={2}
          >
            Developer Program Member
          </BorderBox>
        </MetaComponent>
      )}
      <Text
        as={"p"}
        mb={3}
        fontSize={2}
        color={style === "dark" ? "white" : "gray.5"}
      >
        <Emoji text={bio || ""} />
      </Text>
      <Box mb={6} fontSize={4}>
        {company && (
          <MetaComponent color={theme.iconColor}>
            <StyledOcticon
              color={theme.iconColor}
              icon={Organization}
              size={20}
              verticalAlign="middle"
              mr={2}
              ariaLabel="Location"
            />
            {company}
          </MetaComponent>
        )}
        {location && (
          <MetaComponent color={theme.iconColor}>
            <StyledOcticon
              color={theme.iconColor}
              icon={Location}
              size={20}
              verticalAlign="middle"
              mr={2}
              ariaLabel="Location"
            />
            {location}
          </MetaComponent>
        )}
        {email && (
          <MetaComponent>
            <StyledOcticon
              color={theme.iconColor}
              icon={Mail}
              size={20}
              verticalAlign="middle"
              mr={2}
              ariaLabel="email"
            />
            <PrimerLink href={`mailto:${email}`} color={theme.iconColor}>
              {email}
            </PrimerLink>
          </MetaComponent>
        )}
        {websiteUrl && (
          <MetaComponent>
            <StyledOcticon
              color={theme.iconColor}
              icon={Link}
              size={20}
              verticalAlign="middle"
              mr={2}
              ariaLabel="email"
            />
            <PrimerLink href={websiteUrl} color={theme.iconColor}>
              {websiteUrl}
            </PrimerLink>
          </MetaComponent>
        )}
        {isHireable && (
          <BorderBox
            display={"inline-block"}
            fontSize={5}
            borderRadius={2}
            bg={"green.5"}
            py={1}
            px={2}
            title="Hire me"
            border={0}
          >
            Available for hire
          </BorderBox>
        )}
        <StyledHr color={style} />
        <Box mb={1}>
          <Heading
            mb={2}
            fontSize={4}
            style={{ color: style === "dark" ? "white" : "black" }}
          >
            Organizations
          </Heading>
          {organizations &&
            organizations.nodes.map(({ avatarUrl }) => (
              <PrimerLink
                className={"avatar-group-item"}
                style={{ cursor: "pointer" }}
                key={avatarUrl}
              >
                <Avatar size={35} mr={2} src={avatarUrl} />
              </PrimerLink>
            ))}
        </Box>
        <Box mt={16}>
          <Toggle
            defaultChecked={style === "dark"}
            onChange={onThemeChange}
            icons={{
              checked: (
                <img
                  alt="dark-theme"
                  src="data:image/png;base64,"
                  width={16}
                  height={16}
                />
              ),
              unchecked: (
                <img
                  alt="light-theme"
                  src="data:image/png;base64,"
                  width={16}
                  height={16}
                />
              )
            }}
          />
        </Box>
      </Box>
    </>
  );
};
